rs\",{\n                        id:session.get(\"projectId\")\n                    }),\n                    net.get(\"/user/sendinfo\")\n                ]).then(function (data) {\n                    var obj1=data[0];\n                    var obj2=data[1];\n                    var user;\n                    if(obj1.code==200)\n                    {\n                        user=obj1.data.filter(function (obj) {\n                            if(obj._id==session.get(\"id\"))\n                            {\n                                return false;\n                            }\n                            else\n                            {\n                                return true;\n                            }\n                        });\n                    }\n                    else\n                    {\n                        $.tip(obj1.msg,0);\n                        return;\n                    }\n                    if(obj2.code==200)\n                    {\n                        if(!obj2.data.user)\n                        {\n                            $.tip(\"发件账户不存在，请前去个人设置里面设置\",0);\n                            return;\n                        }\n                    }\n                    else\n                    {\n                        $.tip(obj2.msg,0);\n                        return;\n                    }\n                    $.showBox(_this,require(\"./sendMail.vue\"),{\n                        source:user,\n                        id:_this.interfaceEdit._id\n                    })\n                })\n            },\n            copyClipboard:function () {\n                var ele=document.getElementById(\"shareUrl\");\n                ele.disabled=false;\n                ele.select();\n                document.execCommand(\"Copy\");\n                ele.disabled=true;\n                $.tip(\"已复制到剪贴板\",1);\n            },\n            editRemark:function () {\n                var _this=this;\n                $.inputMul(this,\"编