    root:true\n                                })\n\n                            }\n                        })\n                    })\n                }\n                else\n                {\n                    $.confirm(\"确定退出该工程？\",function () {\n                        _this.deletePending=true;\n                        net.delete(\"/test/quit\",{\n                            project:session.get(\"projectId\")\n                        }).then(function (data) {\n                            _this.deletePending=false;\n                            if(data.code==200)\n                            {\n                                $.tip(\"退出成功\",1);\n                                _this.$store.dispatch(\"changeToList\",null,{\n                                    root:true\n                                })\n                            }\n                            else\n                            {\n                                $.tip(data.msg,0);\n                            }\n                        })\n                    })\n                }\n            },\n            transfer:function () {\n                var _this=this;\n                $.showBox(_this,require(\"../../info/setting/component/transfer.vue\"),{\n                    source:this.$store.state.test.users,\n                    type:\"test\"\n                });\n            },\n            editRemark:function () {\n                var _this=this;\n                $.inputMul(this,\"编辑remark\",function (val) {\n                    _this.test.dis=val;\n                    return true;\n                },1,this.test.dis)\n            },\n            invite:function () {\n                var _this=this;\n                this.invitePending=true;\n                net.post(\"/test/user\",{\n                    project:session.get(\"projectId\"),\n                    user:_this.name\n                }).then(function (data) {\n                    _this.invitePending=false;\n                    if(data.code==200)\n                    {\n                        $.tip(\"修改成功\",1);\n                        _this.users.push(data.data);\n                    }\n                    else\n                    {\n                        $.tip(data.msg,0);\n                    }\n                })\n            },\n            remove:function (item,index) {\n                var _this=this;\n                $.confirm(\"是否踢出该成员！\",function () {\n                    var loading=_this.$loading({fullscreen:true});\n                    net.delete(\"/test/user\",{\n                        project:session.get(\"projectId\"),\n                        user:item._id\n                    }).then(function (data) {\n                        loading.close();\n                        if(data.code==200)\n                        {\n                            $.tip(\"踢出成功\",1);\n                            var index=-1;\n                            _this.users.splice(index,1);\n                        }\n                        else\n                        {\n                            $.tip(data.msg,0);\n                        }\n                    })\n                })\n            },\n            editTeamApply:function () {\n                var _this=this;\n                $.inputMul(this,\"编辑申请信息\",function (val) {\n                    _this.teamDis=val;\n                    return true;\n                },1,this.teamDis)\n            },\n            applyTeam:function () {\n                if(!this.teamId)\n                {\n                    $.tip(\"请输入团队ID\",0);\n                    return;\n                }\n                var _this=this;\n                this.applyPending=true;\n                net.put(\"/team/testapply\",{\n                    id:this.teamId,\n                    project:session.get(\"projectId\"),\n                    dis:this.teamDis\n                }).then(function (data) {\n                    _this.applyPending=false;\n                    _this.teamId=\"\";\n                    _this.teamDis=\"\";\n                    if(data.code==200)\n                    {\n                        $.tip(\"已发送申请，等待团队管理员响应\",1);\n                    }\n                    else\n                    {\n                        $.tip(data.msg,0);\n                    }\n                })\n            },\n            quitTeam:function () {\n                var _this=this;\n                $.confirm(\"是否退出该团队\",function () {\n                    _this.quitPending=true;\n                    net.delete(\"/team/test\",{\n                        id:session.get(\"teamId\"),\n                        project:session.get(\"projectId\")\n                    }).then(function (data) {\n                        _this.quitPending=false;\n                        if(data.code==200)\n                        {\n                            $.tip(\"退出成功\",1);\n                            _this.$store.dispatch(\"changeToList\",null,{\n                                root:true\n                            })\n                        }\n                        else\n                        {\n                            $.tip(data.msg,0)\n                        }\n                    })\n                })\n            },\n        }\n    }\n</script>\n\n\n\n\n\n\n\n\n\n\n"],"sourceRoot":""}]);

