a.code==200)\n                        {\n                            $.tip(\"删除成功\",1);\n                            _this.$store.state.notice.splice(index,1);\n                        }\n                        else\n                        {\n                            $.tip(data.msg,0);\n                        }\n                    })\n                })\n            },\n            moreNotice:function () {\n                var _this=this;\n                this.moreLoading=true;\n                net.get(\"/team/notice\",{\n                    id:this.$store.state.teamId,\n                    page:++_this.page\n                }).then(function (data) {\n                    _this.moreLoading=false;\n                    if(data.code==200)\n                    {\n                        if(data.data.length>0)\n                        {\n                            _this.$store.state.notice.concat(data.data);\n                        }\n                        else\n                        {\n                            _this.more=false;\n                            $.tip(\"已经到最底部啦\",1);\n                        }\n                    }\n                    else\n                    {\n                        $.tip(data.msg,0);\n                    }\n                })\n            },\n            addNotice:function () {\n                var _this=this;\n                $.inputMul(this,\"请输入公告\",function (val) {\n                    if(!val)\n                    {\n                        $.tip(\"请输入公告\",0);\n                        return false;\n                    }\n                    net.post(\"/team/notice\",{\n                        id:_this.$store.state.teamId,\n                        content:val\n                    }).then(function (data) {\n                        if(data.code==200)\n                        {\n                            $.tip(\"添加成功\",1);\n                            _this.$store.state.notice.unshift(data.data);\n                   