\" style=\"height: 50px;text-align: center;vertical-align: middle\">\n                                <td style=\"width:10%\">\n                                    <el-radio class=\"radio\" :label=\"index\" v-model=\"selIndex\">\n                                    </el-radio>\n                                </td>\n                                <td style=\"width: 50%\">\n                                    {{item.name}}\n                                </td>\n                                <td style=\"width: 40%\">\n                                    {{item.createdAt}}\n                                </td>\n                            </tr>\n                        </template>\n                    </table>\n                    <div style=\"text-align: center\" v-else>暂无老模板</div>\n                </el-tab-pane>\n            </el-tabs>\n        </el-row>\n        <el-row class=\"dialog-footer\" slot=\"footer\">\n            <el-button type=\"primary\" @click=\"save\" size=\"small\" :loading=\"savePending\">\n                确定\n            </el-button>\n        </el-row>\n    </el-dialog>\n</template>\n\n<style>\n    #saveTemplate td{\n        border-bottom:1px solid #BBB;\n    }\n    #saveTemplate th{\n        border-bottom:1px solid #BBB;\n    }\n    #saveTemplate .el-radio__label {\n        display: none;\n    }\n    #saveTemplate .el-tabs__content {\n        padding: 10px 5px 20px 10px;\n    }\n</style>\n\n<script>\n    module.exports={\n        props:[],\n        data:function () {\n            return {\n                showDialog:false,\n                type:0,\n                name:this.$store.state.interfaceEdit.name,\n                savePending:false,\n                selIndex:0\n            }\n        },\n        computed:{\n            interface:function () {\n                return this.$store.state.interfaceEdit;\n            },\n            arr:function () {\n                return this.$store.getters.template\n            }\n        },\n        methods:{\n            save:function () {\n                if(this.type==0)\n                {\n                    if(!this.name)\n                    {\n                        $.tip(\"请输入模板名称\",0);\n                        return;\n                    }\n                }\n                else if(this.type==1)\n                {\n                    if(this.arr.length==0)\n                    {\n                        $.tip(\"暂无老模板\",0);\n                        return;\n                    }\n                }\n                this.savePending=true;\n                var _this=this;\n                var query={\n                    name:this.name\n                }\n                if(this.type==1)\n                {\n                    query.id=this.arr[this.selIndex]._id;\n                }\n                this.$store.dispatch(\"saveTemplate\",query).then(function (data) {\n                    _this.savePending=false;\n                    if(data.code==200)\n                    {\n                        $.tip(\"保存成功\",1);\n                        _this.arr.unshift({\n                            _id:data.data._id,\n                            name:data.data.name,\n                            createdAt:data.data.createdAt\n                        })\n                        _this.showDialog=false;\n                    }\n                    else\n                    {\n                        $.tip(data.msg,0)\n                    }\n                })\n\n            }\n        }\n    }\n</script>\n"],"sourceRoot":""}]);

